export default {
	data() {
		return {
			userName:"",//用户名
			password:"",//密码
			account:[],//用户数据
		}
	},
	methods: {
		back(){
			uni.navigateBack({
				delta: 1
			});
		},
		toMyAccount(name,pwd){
			if(!this.check(name,pwd)){
				return;
			}
			uni.request({
				url: `${this.$url}/user/registOrLogin?&&qq=40699904`,
				method: 'POST',
				data: {username:name,password:pwd},
				success: res => {
					this.account = res.data.data;
					console.log(res.data);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		check(name,pwd){
			if(name===''){
				uni.showToast({
					title: '未填写用户名！'
				});
				return false;
			}
			else if(pwd===''){
				uni.showToast({
					title: '未填写密码！'
				});
				return false;
			}
			else{
				return true;
			}
		},
	}
}
